.mainSite {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100vw;
    height: 100vh;
    gap: 1em;
    background-color: white;
    border-collapse: separate;
    background-color: rgb(84, 83, 86);
    padding: 1em;
}

.mainSite>* {
    border-radius: 1em;

    overflow-y: scroll;
    background-color: rgb(237, 139, 65);
    padding: 1em;
}