.App {
  text-align: center;
  width: 30%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

input {
  width: 20em;
}

button {
  width: 5em;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #da141400;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 1em;
  background: #af151595;
}

.addButton {
  transition-duration: 0.4s;
}

.addButton:hover {
  background-color: rgb(120, 186, 21);
}

.editButton {
  transition-duration: 0.4s;
}

.editButton:hover {
  background-color: rgb(90, 170, 240);
}

.deleteButton {
  transition-duration: 0.4s;
}

.deleteButton:hover {
  background-color: rgb(229, 31, 31);
}